.Header {
  text-align: center;
}

.Header-logo {
  -webkit-animation: Header-logo-spin infinite 20s linear;
          animation: Header-logo-spin infinite 20s linear;
  height: 80px;
}

.Header-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.Header-title {
  font-size: 1.5em;
}

@-webkit-keyframes Header-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Header-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



